footer {
    background-color: $footer-primary-background-color;
    color: $footer-primary-text-color;
    font-size: 16px;
    line-height: 24px;
    .container {
        padding: 60px 12px 32px 12px;
        @include pad {
            padding: 56px 36px 40px 36px;
        }
        @include mobile {
            padding: 24px 14px;
        }
    }
    .footer-content {
        border-bottom: 1px solid #FFFFFF;
        @include pad {
            padding-bottom: 32px;
        }
        @include mobile {
            display: flex;
            flex-direction: row-reverse;
            justify-content: space-between;
            align-items: start;
            gap: 8px 0;
            padding-bottom: 24px;
        }
    }
    .footer-menu {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .webmap-list {
        display: flex;
        gap: 48px;
        @include mobile {
            display: none;
        }
        .webmap-link {
            color: #fff;
        }
    }
    .social-list {
        display: flex;
        justify-content: space-between;
        gap: 32px;
        @include mobile {
            gap: 8px;
        }
        img {
            width: 48px;
            height: 48px;
            @include mobile {
                width: 24px;
                height: 24px;
            }
        }
    }
    .contact {
        font-size: 36px;
        line-height: 54px;
        padding: 36px 0 32px 0;
        gap: 8px 0;
        @include mobile {
            padding: 32px 0;
        }
        @include mobile {
            font-size: 16px;
            line-height: 24px;
            padding: 0px;
        }
        li {
            display: flex;
            align-items: center;
            gap: 22px;
            @include mobile {
                gap: 11px;
            }
        }
        a {
            color: #fff;
        }
    }
    .copyright {
        display: flex;
        justify-content: space-between;
        padding-top: 32px;
        @include mobile {
            display: block;
            padding-top: 16px;
            gap: 8px 0px;
        }
        ul {
            display: flex;
            gap: 48px;
            @include mobile {
                display: block;
            }
        }
    }
    a {
        color: $nav-menu-text-color;
    }
    a:hover {
        color: $nav-menu-text-color--hover;
    }
}